import React, { useEffect, useState } from 'react';
import axios from 'axios';
import '../css/Signup.css';
import { Form, Button } from 'react-bootstrap';

function Signup() {
  const [csrfToken, setCsrfToken] = useState('');
  const [userCreateForm, setUserCreateForm] = useState({
    userid: '',
    password: '',
    passwordConfirmation: '',
    username:'',
    phoneNumber: '',
    email: ''
  });
  const [errorMessage, setErrorMessage] = useState('');

  useEffect(() => {
    //  CSRF  .
    axios.get('/api/csrf')
      .then(response => {
        setCsrfToken(response.data.token || ''); // CSRF  
      })
      .catch(error => {
        console.error('Error fetching CSRF token:', error);
      });
  }, []);

  const handleSubmit = async (event) => {
    event.preventDefault();


    //   
    if (userCreateForm.password.length < 8) {
      setErrorMessage(' 8  .');
      return;
    }

    //       
    if (userCreateForm.password !== userCreateForm.passwordConfirmation) {
      setErrorMessage('  .');
      return;
    }

    try {
      //    
      const response = await axios.post('/user/signup', userCreateForm, {
        headers: {
          'X-XSRF-Token': csrfToken,
          'Content-Type': 'application/json'
        }
      });
        window.alert(' .');
        window.location.href = '/';
    } catch (error) {
      console.error('Signup failed:', error);
    }
  };

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setUserCreateForm(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  return (
    <div>
      <h2></h2>
      <Form onSubmit={handleSubmit}>
        <input type="hidden" name="_csrf" value={csrfToken} />
        <Form.Group controlId="formBasicUserid">
          <Form.Label>ID</Form.Label>
          <Form.Control type="text" placeholder="ID (3 , 25 )" name="userid" value={userCreateForm.userid} onChange={handleInputChange} minLength={3} maxLength={25} required />
        </Form.Group>

        <Form.Group controlId="formBasicPassword">
          <Form.Label></Form.Label>
          <Form.Control type="password" placeholder=" (8 )" name="password" value={userCreateForm.password} onChange={handleInputChange} minLength={8} required />
        </Form.Group>

        <Form.Group controlId="formBasicPasswordConfirmation">
          <Form.Label> </Form.Label>
          <Form.Control type="password" placeholder=" " name="passwordConfirmation" value={userCreateForm.passwordConfirmation} onChange={handleInputChange} required />
        </Form.Group>

        <Form.Group controlId="formBasicUsername">
            <Form.Label></Form.Label>
            <Form.Control type="text" placeholder=" (3 , 25 )" name="username" value={userCreateForm.username} onChange={handleInputChange} minLength={3} maxLength={25} required />
        </Form.Group>

        <Form.Group controlId="formBasicPhoneNumber">
          <Form.Label></Form.Label>
          <Form.Control type="tel" placeholder="" name="phoneNumber" value={userCreateForm.phoneNumber} onChange={handleInputChange} required />
        </Form.Group>

        <Form.Group controlId="formBasicEmail">
          <Form.Label></Form.Label>
          <Form.Control type="email" placeholder="" name="email" value={userCreateForm.email} onChange={handleInputChange} required />
        </Form.Group>

        <Button variant="primary" type="submit">
          
        </Button>
        {errorMessage && <p style={{ color: 'red' }}>{errorMessage}</p>}
      </Form>
    </div>
  );
}

export default Signup;
