import React, { useState, useEffect } from 'react';
import { Container, Nav, Navbar, NavDropdown, Form, Row, Col, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom'; // React Router Link .
import axios from 'axios';

import '../css/App.css';

function NavbarComponent() {

  //   js
  useEffect(() => {
    const handleSidebarToggle = (event) => {
      event.preventDefault();
      document.body.classList.toggle('sb-sidenav-toggled');
      localStorage.setItem('sb|sidebar-toggle', document.body.classList.contains('sb-sidenav-toggled'));
    };

    const sidebarToggle = document.body.querySelector('#sidebarToggle');
    if (sidebarToggle) {
      sidebarToggle.addEventListener('click', handleSidebarToggle);

      return () => {
        sidebarToggle.removeEventListener('click', handleSidebarToggle);
      };
    }
  }, []);

  //    .
  const [isAuthenticated, setIsAuthenticated] = useState(
      localStorage.getItem('isAuthenticated') === 'true'
    );

  //  
  const handleLogout = () => {
    axios.get('/user/logout')
    .then(response => {
      //      
      console.log(response);
      //    isAuthenticated  false     
      setIsAuthenticated(false);
      localStorage.removeItem('isAuthenticated');
      window.location.reload();
    })
    .catch(error => {
      //     
      console.error('Logout failed:', error);
    });
  };

  return (

    <Navbar expand="lg" className="bg-body-tertiary border-bottom">
      <Container fluid>
        <Button variant="secondary" id="sidebarToggle"></Button>
        
        <Form className="search-form">
          <Row>
            <Col xs="auto">
              <Form.Control
                type="text"
                placeholder=""
                className="mr-sm-2"
              />
            </Col>
            <Col xs="auto">
              <Button type="submit"></Button>
            </Col>
          </Row>
        </Form>

        <Navbar.Toggle aria-controls="navbarSupportedContent" />
        <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">
          <Nav>
            {isAuthenticated  ? (
              <>
                <NavDropdown title="" id="basic-nav-dropdown">
                  <Link to="/mypage" className="dropdown-item"> </Link>
                  <Link to="/mypagedelete" className="dropdown-item"> </Link>
                  <NavDropdown.Divider />
                  <Nav.Link onClick={handleLogout}></Nav.Link>
                </NavDropdown>
              </>
            ) : (
              <>
                <Link to="/user/login" className="nav-link"></Link>
                <Link to="/user/signup" className="nav-link"></Link>
              </>
            )}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavbarComponent;
